<template>
  <el-card class="table-card">
    <div class="header" slot="header">
      <span>路由管理</span>
      <el-button @click="insert_dialog_visible = true" plain type="primary" icon="el-icon-circle-plus-outline">
        添加操作按钮
      </el-button>
    </div>
    <el-table :data="tableData">
      <el-table-column prop="operation_id" label="#" width="40">
      </el-table-column>
      <el-table-column prop="operation_name" label="操作名称">
      </el-table-column>
      <el-table-column prop="operation_code" label="操作代码">
      </el-table-column>
      <el-table-column prop="operation_description" label="操作描述">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="openEditDialog(scope.row)" icon="el-icon-edit" type="primary" plain>编辑</el-button>
          <el-button @click="handleRemove(scope.row.operation_id,scope.$index)" icon="el-icon-delete" type="danger"
                     plain>
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加链接Dialog -->
    <el-dialog title="添加操作按钮" :visible.sync="insert_dialog_visible" @close="handleDialogClose('insert_form')">
      <el-form ref="insert_form" :model="insert_form" :rules="rules" label-width="80px">
        <el-form-item label="操作名称" prop="operation_name">
          <el-input v-model="insert_form.operation_name" placeholder="请输入操作按钮名称"></el-input>
        </el-form-item>
        <el-form-item label="操作代码" prop="operation_code">
          <el-input v-model="insert_form.operation_code" placeholder="请输入英文的操作代码"></el-input>
        </el-form-item>
        <el-form-item label="操作描述" prop="operation_description">
          <el-input v-model="insert_form.operation_description" placeholder="请输入操作按钮的说明文字"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
				<el-button @click="insert_dialog_visible = false">取 消</el-button>
				<el-button type="primary" @click="handleInsert">添 加</el-button>
			</span>
    </el-dialog>
    <!-- 编辑链接Dialog -->
    <el-dialog title="编辑操作按钮" :visible.sync="edit_dialog_visible" @close="handleDialogClose('edit_form')">
      <el-form ref="edit_form" :model="edit_form" :rules="rules" label-width="80px">
        <el-form-item label="操作名称" prop="operation_name">
          <el-input v-model="edit_form.operation_name"></el-input>
        </el-form-item>
        <el-form-item label="操作代码" prop="operation_code">
          <el-input v-model="edit_form.operation_code"></el-input>
        </el-form-item>
        <el-form-item label="操作描述" prop="operation_description">
          <el-input v-model="edit_form.operation_description"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
				<el-button @click="edit_dialog_visible = false">取 消</el-button>
				<el-button type="primary" @click="handleEdit">保 存</el-button>
			</span>
    </el-dialog>
  </el-card>
</template>

<script>
import { Operation } from '@/api/';

export default {
  name: "Operation",
  data() {
    return {
      tableData: [],
      insert_dialog_visible: false,
      edit_dialog_visible: false,
      insert_form: {
        operation_name: '',
        operation_code: '',
        operation_description: '',
      },
      edit_form: {
        operation_name: '',
        operation_code: '',
        operation_description: '',
      },
      current_operation: '',
      rules: {
        operation_name: [{ required: true, message: "请输入操作按钮名称", trigger: 'blur' }],
        operation_code: [{ required: true, message: "请输入英文的操作代码", trigger: 'blur' }],
        operation_description: [{ required: true, message: "请输入操作按钮的说明文字", trigger: 'blur' }],
      },
    }
  },
  created() {
    document.title = "操作按钮管理";
    // 操作按钮列表
    this.loadList();
  },
  methods: {
    // 加载操作按钮列表
    async loadList() {
      let { status, data } = await Operation.list({ pagesize: 30 })
      if (status) {
        this.tableData = data;
      }
    },
    // 添加操作按钮
    handleInsert() {
      this.$refs.insert_form.validate(async (valid) => {
        if (valid) {
          let { status, msg, data } = await Operation.insert(this.insert_form);
          if (status) {
            // 更新DOM
            this.tableData.push({ ...this.insert_form, ...data });
            // 关闭弹窗
            this.insert_dialog_visible = false;
            // 消息提示
            this.$message.success(msg);
          } else {
            this.$message.error(msg);
          }
        }
      });
    },
    // 打开编辑 dialog
    openEditDialog(operation) {
      // 缓存指针地址
      this.current_operation = operation;
      // 还原表单
      this.edit_form = { ...operation };
      // 打开 dialog
      this.edit_dialog_visible = true;
    },
    // 编辑操作按钮
    handleEdit() {
      this.$refs.edit_form.validate(async (valid) => {
        if (valid) {
          let { operation_id } = this.edit_form;
          let { status, msg, data } = await Operation.update(operation_id, this.edit_form);
          if (status) {
            // 更新DOM
            Object.assign(this.current_operation, this.edit_form);
            // 关闭弹窗
            this.edit_dialog_visible = false;
            // 消息提示
            this.$message.success(msg);
          } else {
            this.$message.error(msg);
          }
        }
      });
    },
    // 删除操作按钮
    async handleRemove(operation_id, index) {
      this.$confirm('此操作将永久删除该操作按钮, 是否继续?', { type: 'warning' })
          .then(async () => {
            // 删除操作按钮
            let { status, msg } = await Operation.remove(operation_id);
            if (status) {
              this.tableData.splice(index, 1);
              this.$message.success(msg);
            }
          })
          .catch(() => {
            this.$message.info('取消成功')
          });
    },
    // 关闭dialog弹窗
    handleDialogClose(form) {
      this.$refs[form].resetFields();
    },
  }
}
</script>

<style lang="less" scoped>
.table-card {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
</style>